import React, { useEffect, useState } from 'react';
import { BrowserRouter, Switch, Route, HashRouter, Redirect } from 'react-router-dom';
import Home from '../pages/Home';
import Error404 from '../pages/404';
import Mixes from '../pages/Mixes';
import Contacto from '../pages/Contacto';
import Recetas from '../pages/Recetas';
import MasTips from '../pages/MasTips';
import Receta from '../pages/Receta';
import Prueba from '../pages/Prueba';
import ScrollToTop from '../components/ScrollToTop';
import Footer from '../components/Footer';
import run from '../run';
import { setManualCookie } from '../localFunctions';

const App = () => {
  setManualCookie(6);
  const [error, setError] = useState(null);
  const [hizoClick, setHizoClick] = useState(false);
  useEffect(() => {
    run(setError);
  }, []);
  if (error) {
    return <Error404 typeError={error} />;
  }
  return (
    <HashRouter>
      <Switch>
        <Route exact path='/'>
          <ScrollToTop />
          <Home />
        </Route>
        <Route path='/mixes'>
          <ScrollToTop />
          <Mixes />
        </Route>
        <Route path='/recetas'>
          <ScrollToTop />
          <Recetas />
        </Route>
        <Route path='/receta'>
          <ScrollToTop />
          <Receta />
        </Route>
        <Route path='/masTips'>
          <ScrollToTop />
          <MasTips />
        </Route>
        <Route path='/contacto'>
          <ScrollToTop />
          <Contacto />
        </Route>
        <Route path='/prueba'>
          <ScrollToTop />
          <Prueba setHizoClick={setHizoClick} />
        </Route>
      </Switch>
      <Footer />
    </HashRouter>
  );
};

export default App;
