export const getCookie = (cname) => {
  const name = `${cname}=`;
  const ca = document.cookie.split(';');
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return '';
};

export const setCookie = (cname, cvalue, exdays) => {
  const d = new Date();
  d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
  const expires = `expires=${d.toUTCString()}`;
  document.cookie = `${cname}=${cvalue};${expires};path=/`;
};

export const animateCSS = (element, animationName, callback) => {
  const node = document.querySelector(element);
  const animationNames = animationName.split(' ');
  node.classList.add('animated');
  for (let i = 0; i < animationNames.length; i++) {
    node.classList.add(animationNames[i]);
  }
  const handleAnimationEnd = () => {
    node.classList.remove('animated');
    for (let i = 0; i < animationNames.length; i++) {
      node.classList.remove(animationNames[i]);
    }
    node.removeEventListener('animationend', handleAnimationEnd);

    if (typeof callback === 'function') callback();
  };

  node.addEventListener('animationend', handleAnimationEnd);
};

export const setSubMixes = (valor) => {
  localStorage.setItem('subMixes', valor);
  setCookie('subMixes', valor, 365);
};

export const cancelApertura = () => {
  if (navigator.cookieEnabled === true) {
    setCookie('apertura', 0, 365);
  } else {
    if (typeof (Storage) !== 'undefined') {
      localStorage.setItem('apertura', 0);
    }
  }
};

export const go = (element, callback) => {
  animateCSS(element, 'fadeOut faster', () => {
    callback();
  });
};

export const postWebService = (dato, usuario = '&suid=glodiser', registro = '') => {
  const getXHR = () => {
    if (window.XMLHttpRequest) {
      // Chrome, Firefox, IE7+, Opera, Safari
      return new XMLHttpRequest();
    }
    // IE6
    try {
      // The latest stable version. It has the best security, performance,
      // reliability, and W3C conformance. Ships with Vista, and available
      // with other OS's via downloads and updates.
      return new ActiveXObject('MSXML2.XMLHTTP.6.0');
    } catch (e) {
      try {
        // The fallback.
        return new ActiveXObject('MSXML2.XMLHTTP.3.0');
      } catch (e) {
        console.log('This browser is not AJAX enabled.');
        return null;
      }
    }
  };
  const xhttp = getXHR();
  xhttp.onreadystatechange = function () {
    if (xhttp.readyState === XMLHttpRequest.DONE) {
      console.log(xhttp.responseText);
    }
  };
  ///n_customnonvisual.svc/of_click
  xhttp.open('GET', `http://www.chozasdigital.com.ar/wsreport/n_customnonvisual.svc/of_click?${dato}${usuario}${registro}`, true);
  xhttp.send(null);
};

