import React, { useEffect } from 'react';
import Portal from './Portal';
import like from '../assets/static/like.png';
import '../assets/styles/modal.css';

const Modal = ({ isOpen, onClose }) => {

  if (!isOpen) {
    return null;
  }

  useEffect(() => {
    document.querySelector('.Modal').addEventListener('click', (e) => {
      if (e.target === document.querySelector('.Modal')) {
        onClose();
      }
    });
  }, []);

  return (
    <Portal>
      <div className='Modal animated fadeIn faster'>
        <div className='Modal__container text-center animated slideInDown faster rounded-lg overflow-auto'>
          <div className='p-4 flex justify-between border-b border-gray-400'>
            <img src={like} alt='corazon' />
            <div>
              <button type='button' onClick={onClose}>
                <span className='font-bold text-gray-600 text-2xl'>√ó</span>
              </button>
            </div>
          </div>
          <div className='p-4'>
            ¬°Nos alegra que te guste!
            <br />
            ¬°Muchas Gracias!
          </div>
        </div>
      </div>
    </Portal>
  );
};

export default Modal;
