import React, { useRef } from 'react';
import { useHistory } from 'react-router-dom';
//import { navigate } from 'gatsby';
import { animateCSS, setSubMixes, cancelApertura, go } from '../localFunctions';
import packBizcochuelo from '../assets/static/pack_bizcochuelo.png';
import packChantilly from '../assets/static/pack_chantilly.png';
import packMerengue from '../assets/static/pack_merengue.png';
import packMousse from '../assets/static/pack_mousse.png';
import packPastelera from '../assets/static/pack_pastelera.png';
import packSablee from '../assets/static/pack_sablee.png';
import mixes from '../assets/static/btn_mixeskeuken.png';
import { Header, SEO } from '../components';

const Mixes = () => {
  const history = useHistory();
  const navigate = history.push;
  const cantPack = useRef([{}, {}, {}, {}, {}, {}]);
  const goTo = (mixIs) => {
    setSubMixes(mixIs);
    cancelApertura();
    animateCSS('.wrapper', 'fadeOut fast', () => {
      navigate('/');
    });
  };

  return (
    <>
      <SEO title='Mixes' />
      <div className='mixes'>
        <Header>
          <div className='home_header-titulo text-center mt-4'>
            <div className='flex justify-center'>
              <img className='object-contain w-20 h-12' src={mixes} alt='Mixes Keuken' />
            </div>
            <b className='text-white gotham-l'>CONOC√â TODA NUESTRA L√çNEA</b>
          </div>
          <div />
        </Header>
        <section className='wrapper animated fadeIn'>
          <main className='grid grid-cols-2'>
            {cantPack.current.map((a, index) => {
              const id = index;
              let img,
                alt;
              switch (index) {
                case 0:
                  img = packBizcochuelo;
                  alt = 'Bizcochuelo';
                  break;
                case 1:
                  img = packChantilly;
                  alt = 'Chantilly';
                  break;
                case 2:
                  img = packPastelera;
                  alt = 'Pastelera';
                  break;
                case 3:
                  img = packMerengue;
                  alt = 'Merengue';
                  break;
                case 4:
                  img = packMousse;
                  alt = 'Mousse';
                  break;
                case 5:
                  img = packSablee;
                  alt = 'Sablee';
                  break;
              }
              return (
                <div key={id}>
                  <button type='button' onClick={() => goTo(index + 1)}>
                    <div>
                      <img src={img} alt={alt} />
                    </div>
                  </button>
                </div>
              );
            })}
          </main>
        </section>
      </div>
    </>
  );
};

export default Mixes;
