import React, { useEffect, useState } from 'react';
import { getCookie } from '../localFunctions';
import qrBizcochuelo from '../assets/static/bizcochuelo.png';
import qrChantilly from '../assets/static/chantilly.png';
import qrMerengue from '../assets/static/merengue.png';
import qrMixsablee from '../assets/static/mixsablee.png';
import qrMousse from '../assets/static/mousse.png';
import qrPastelera from '../assets/static/pastelera.png';
import instagram from '../assets/static/instagram.png';
import facebook from '../assets/static/facebook.png';
import youtube from '../assets/static/youtube.png';
import lodiser from '../assets/static/lodiser.png';
import giraPantalla from '../assets/static/gira_tu_pantalla.png';
import { SEO } from '../components';

const Error404 = ({ typeError }) => {
  const [qr, setQr] = useState(null);
  const [isDesktop, setIsDesktop] = useState(false);
  useEffect(() => {
    const changeImgQr = () => {
      let mixesGuardado = null;
      let mixElegido = null;
      mixesGuardado = parseInt(getCookie('mixes'), 10);
      if (!mixesGuardado) {
        mixElegido = mixesGuardado;
      }
      switch (mixElegido) {
        case 1:
          setQr(qrBizcochuelo);
          break;
        case 2:
          setQr(qrChantilly);
          break;
        case 3:
          setQr(qrMerengue);
          break;
        case 4:
          setQr(qrMixsablee);
          break;
        case 5:
          setQr(qrMousse);
          break;
        case 6:
          setQr(qrPastelera);
          break;
        default:
          setQr(qrBizcochuelo);
          break;
      }
    };
    changeImgQr();
  }, []);

  useEffect(() => {
    const verifyScreen = () => {
      if (typeError === 'desktop') {
        setIsDesktop(true);
      } else if (typeError === 'girarPantalla') {
        setIsDesktop(false);
      }
    };
    verifyScreen();
  }, [typeError]);

  return (
    <div className='animated fadeIn'>
      { isDesktop ? (
        <>
          <SEO title='Escanear' />
          <div className='container mx-auto'>
            <h1 className='text-center text-6xl text-red-700 font-poppins'>¬°Escanea el c√≥digo!</h1>
            <section className='flex justify-center py-4'>
              <img className='object-contain w-64' src={qr} alt='QR Bizcochuelo' />
            </section>
            <div>
              <div className='flex justify-around'>
                <div className='flex flex-col'>
                  <a className='hover:underline hover:text-blue-800 text-blue-600' href='mailto:info@lodiser.com.ar'>info@lodiser.com.ar</a>
                  <a className='mt-1 hover:underline hover:text-blue-800 text-blue-600' href='http://www.lodiser.com.ar'>www.lodiser.com.ar</a>
                </div>
                <div>
                  <img className='object-contain w-16' src={lodiser} alt='Logo Lodiser' />
                </div>
              </div>
              <div className='grid grid-cols-3 space-x-16 mt-4'>
                <div className='flex justify-center items-center'>
                  <a href='https://www.facebook.com/Lodiser/'>
                    <img className='object-contain h-24' src={facebook} alt='facebook' />
                  </a>
                </div>
                <div className='flex justify-center items-center'>
                  <a href='https://www.instagram.com/lodiser.arg/'>
                    <img className='object-contain h-24' src={instagram} alt='instagram' />
                  </a>
                </div>
                <div className='flex justify-center items-center'>
                  <a href='https://www.youtube.com/channel/UCKB3uKtC5jdevAz6MdqhhzQ'>
                    <img className='object-contain h-24' src={youtube} alt='youtube' />
                  </a>
                </div>
              </div>
              <p className='text-center mt-4 text-gray-600 font-light gotham-m'>
                Elaborado por Lodiser S.A.
                {' '}
                <span>|</span>
                {' '}
                Tronador 71
                (C1427CRA) CABA
              </p>
            </div>
          </div>
        </>
      ) :
        (
          <>
            <SEO title='Gire la Pantalla' />
            <div>
              <div className='flex justify-center items-center h-screen'>
                <img className='object-contain w-full' src={giraPantalla} alt='Girar Pantalla' />
              </div>
            </div>
          </>
        )}
    </div>
  );
};

export default Error404;
