import React from 'react';
import { useHistory } from 'react-router-dom';
//import { navigate } from 'gatsby';
import btnRecetas from '../assets/static/btn_recetas.png';
import btnContacto from '../assets/static/btn_contacto.png';
import { go } from '../localFunctions';
import '../assets/styles/footer.css';

const Footer = () => {
  const history = useHistory();
  const navigate = history.push;
  return (
    <footer className='footer-bg bottom-0 left-0 px-4 sticky'>
      <nav>
        <ul className='grid grid-cols-3'>
          <li className='flex justify-center pb-4 items-end sm:items-center p-3'>
            <button className='gotham-l text-white text-xs' type='button' onClick={() => go('.wrapper', () => navigate('/Mixes'))}>
              <b>CONOC√â TODA LA L√çNEA</b>
            </button>
          </li>
          <li className='flex justify-center items-center pb-1'>
            <button className='w-24' type='button' to='/recetas' onClick={() => go('.wrapper', () => navigate('/Recetas'))}>
              <img className='object-contain' src={btnRecetas} alt='Recetas' />
            </button>
          </li>
          <li className='flex items-end pb-2'>
            <button className='w-20' type='button' to='/contacto' onClick={() => go('.wrapper', () => navigate('/Contacto'))}>
              <img className='object-contain' src={btnContacto} alt='Contacto' />
            </button>
          </li>
        </ul>
      </nav>
    </footer>
  );
};

export default Footer;
