import React, { useRef } from 'react';
import { useHistory } from 'react-router-dom';
//import { navigate } from 'gatsby';
import Swal from 'sweetalert2';
import { RadioButton, SEO } from '../components';
import { go, postWebService } from '../localFunctions';
import '../assets/styles/contacto.css';
import homePositiva from '../assets/static/home_positiva.png';
import btnPositivo from '../assets/static/btn_enviar_positivo.png';
import btnNegativo from '../assets/static/btn_enviar_negativo.png';
import lodiser from '../assets/static/lodiser.png';

const Contacto = () => {
  const history = useHistory();
  const navigate = history.push;
  const preparacion = useRef([{
    id: 1,
    texto: 'MUY DIF√çCIL',
  }, {
    id: 2,
    texto: 'DIF√çCIL',
  }, {
    id: 3,
    texto: 'SIMPLE',
  }, {
    id: 4,
    texto: 'FACIL',
  }, {
    id: 5,
    texto: 'MUY FACIL',
  },
  ]);
  const resultado = useRef([{
    id: 1,
    texto: 'MALO',
  }, {
    id: 2,
    texto: 'REGULAR',
  }, {
    id: 3,
    texto: 'BUENO',
  }, {
    id: 4,
    texto: 'MUY BUENO',
  }, {
    id: 5,
    texto: 'EXCELENTE',
  },
  ]);

  const handleForm = (e) => {
    e.preventDefault();
    const name = document.querySelector('#name').value;
    const email = document.querySelector('#email').value;
    if (name.length > 0 && name.length <= 80 && email.length > 0 && email.length <= 100) {
      //postWebService('sdesc=000060010300000', '&suid=0', `&sunom=${name}&sumail=${email}`);
      Swal.fire({
        title: '¬°Datos Enviados!',
        icon: 'success',
        showConfirmButton: false,
      });
      setTimeout(() => {
        Swal.close();
      }, 2000);
    }
  };

  const handleRadio = () => {
    const resultado = [];
    const botonRadio = document.getElementsByClassName('preparacionRadio');
    const botonRadio1 = document.getElementsByClassName('resultadoRadio');
    for (let i = 0; i < 5; i++) {
      if (!botonRadio[i].classList.contains('hidden')) {

        resultado[0] = i + 1;
      }
      if (!botonRadio1[i].classList.contains('hidden')) {

        resultado[1] = i + 1;
      }
    }
    if (resultado[0] > 0 && resultado[1] > 0) {
      Swal.fire({
        title: '¬°Datos Enviados!',
        icon: 'success',
        showConfirmButton: false,
      });
      setTimeout(() => {
        Swal.close();
      }, 2000);
      //postWebService('sdesc=000060010400000', '&suid=glodiser', `&stabla1=00100${resultado[0]}&stabla2=00200${resultado[1]}`);
    } else {
      Swal.fire({
        title: 'Falta Seleccionar',
        icon: 'error',
      });
    }
  };

  return (
    <>
      <SEO title='Contacto' />
      <div className='contacto animated fadeIn'>
        <section className='wrapper'>
          <main>
            <div className='flex justify-end px-4 py-1'>
              <button type='button' onClick={() => go('.wrapper', () => navigate('/'))}>
                <img className='object-contain w-6' src={homePositiva} alt='Ir a Home' />
              </button>
            </div>
            <h5 className='text-center text-2xl gotham-m text-mixes-title'>
              ¬øSos fan de la pasteler√≠a?
            </h5>
            <h5 className='text-center text-xl gotham-m text-mixes-title'>
              D√©janos tus datos para enviarte todas las novedades
            </h5>
            <form className='pt-1 pb-2 px-4 grid grid-cols-12' onSubmit={(e) => handleForm(e)}>
              <div className='flex flex-col space-y-3 col-span-9 pr-8'>
                <input className='border border-gray-400 px-2 py-1 rounded placeholder-gray-600' type='text' id='name' placeholder='NOMBRE' name='Nombre' required maxLength='80' />
                <input className='border border-gray-400 px-2 py-1 rounded placeholder-gray-600' type='email' id='email' placeholder='EMAIL' name='email' required maxLength='100' />
              </div>
              <div className='col-span-3 flex justify-center items-center'>
                <button type='submit'>
                  <img className='object-contain w-32' src={btnPositivo} alt='Enviar' />
                </button>
              </div>
            </form>
            <div className='contacto-bg-radio pb-4'>
              <h6 className='text-mixes-subTitle font-bold text-center py-2'>Contanos tu experiencia con Keuken</h6>
              <div className='pt-2 pb-5'>
                <h6 className='text-mixes-bold font-medium pl-2 pb-2'>La preparaci√≥n fue</h6>
                <div className='grid grid-cols-5 px-2'>
                  {preparacion.current.map((paso, index) => {
                    const { id, texto } = paso;
                    return (
                      <RadioButton key={id} id={index} texto={texto} className='preparacionRadio' />
                    );
                  })}
                </div>
              </div>
              <div className='pb-5'>
                <h6 className='text-mixes-bold font-medium pl-2'>El resultado final fue</h6>
                <div className='grid grid-cols-5 px-2'>
                  {resultado.current.map((paso, index) => {
                    const { id, texto } = paso;
                    return (
                      <RadioButton key={id} id={index} texto={texto} className='resultadoRadio' />
                    );
                  })}
                </div>
              </div>
              <div className='flex justify-center'>
                <button type='button' onClick={handleRadio} data-toggle='modal' data-target='.envioPerfecto'>
                  <img className='object-contain w-20' src={btnNegativo} alt='Enviar' />
                </button>
              </div>
            </div>
            <div className='pt-4 pb-1'>
              <div className='grid grid-cols-2 px-2'>
                <div className='flex flex-col'>
                  <a className='text-gray-700 text-sm' href='mailto:info@lodiser.com.ar'>info@lodiser.com.ar</a>
                  <a className='text-gray-700 text-sm' href='http://www.lodiser.com.ar'>www.lodiser.com.ar</a>
                  <div className='grid grid-cols-3 py-2'>
                    <a className='text-gray-700' href='https://www.facebook.com/Lodiser/'><span aria-label='facebook' className='icon-facebook' /></a>
                    <a className='text-gray-700' href='https://www.instagram.com/lodiser.arg/'><span aria-label='instagram' className='icon-instagram' /></a>
                    <a className='text-gray-700' href='https://www.youtube.com/channel/UCKB3uKtC5jdevAz6MdqhhzQ'><span aria-label='youtube' className='icon-youtube' /></a>
                  </div>
                </div>
                <div className='flex justify-end'>
                  <img className='object-contain w-16' src={lodiser} alt='Logo Lodiser' />
                </div>
              </div>
              <div>
                <p className='text-center text-gray-700 text-sm'>
                  Elaborado por Lodiser S.A.
                  <span> | </span>
                  Tronador 71
                  (C1427CRA) CABA
                </p>
              </div>
            </div>
          </main>
        </section>
      </div>
    </>
  );
};

export default Contacto;
