import React, { useEffect, useState } from 'react';
import { Header, SEO } from '../components';
import json from '../jsons/home.json';
import { getCookie, setCookie, cancelApertura, animateCSS } from '../localFunctions';
import '../assets/styles/home.css';
import dedo from '../assets/static/dedo.png';
import tituloPreparacion from '../assets/static/titulo_preparacion.png';
import homeNegativa from '../assets/static/home_negativa.png';

const Home = () => {
  const [visualHome, setVisualHome] = useState(false);
  const [reload, setReload] = useState(false);
  const [dataDOM, setDataDOM] = useState({
    'titulo': '',
    'alt': '',
    'imagen': '',
    'parrafo': [],
    'preparacion': [],
    'vidaUtil': [],
  });

  useEffect(() => {
    let mixes;
    let subMixes = null;
    let apertura = null;
    mixes = parseInt(getCookie('mixes'), 10);
    subMixes = parseInt(getCookie('subMixes'), 10);
    apertura = parseInt(getCookie('apertura'), 10);
    if (subMixes > 0 && apertura !== 1) {
      setVisualHome(true);
    }
    if (apertura !== 1 && (subMixes >= 1 && subMixes <= 6)) {
      mixes = subMixes;
    }
    const mix = json[mixes - 1] || json[0]; //delete este ultimo;;
    setDataDOM({ ...dataDOM, titulo: mix.titulo, alt: mix.alt, imagen: mix.imagen, parrafo: mix.parrafo, preparacion: mix.preparacion, vidaUtil: mix.vidaUtil });
    if (apertura === 1) {
      setCookie('apertura', 0);
      //postWebService('sdesc=000060010100003');
    }
  }, [reload]);

  const reset = () => {
    cancelApertura();
    if (navigator.cookieEnabled === true) {
      setCookie('subMixes', 0, 365);
    }
    setVisualHome(false);
    animateCSS('.home_header-titulo', 'fadeOut faster');
    animateCSS('.wrapper', 'fadeOut faster', () => {
      setReload(true);
      animateCSS('.wrapper', 'fadeIn');
      animateCSS('.home_header-titulo', 'fadeIn');
    });
  };

  return (
    <>
      <SEO title='Home' description='Recetas - Mezclas - Lodiser - MixesKeuken - Todo en un solo lugar' />
      <div className='home'>
        <Header noHome={true}>
          <div className='flex justify-end'>
            <button className={!visualHome ? 'invisible' : null} type='button' onClick={reset}>
              <img className='w-6 object-contain' src={homeNegativa} alt='Volver a home' />
            </button>
          </div>
          <div className='home_header-titulo text-center mt-4 pb-8'>
            <h1 className='text-2xl'>{dataDOM.titulo}</h1>
          </div>
          <div />
        </Header>
        <section className='wrapper pb-1 animated fadeIn'>
          <main>
            <div className='text-center flex flex-col justify-center items-center pt-4 space-y-3 px-4 pb-12'>
              <div className='w-40 ml-10'>
                <img className='object-contain' src={dataDOM.imagen} alt={dataDOM.alt} />
              </div>
              <div>
                <p className='gotham-m text-sm text-gray-700'>
                  {dataDOM.parrafo.map((oracion) => {
                    const { id, texto, isBold } = oracion;
                    return (
                      <span key={id}>
                        {isBold ? <b>{texto}</b> : texto}
                      </span>
                    );
                  })}
                </p>
              </div>
              <div className='w-12'>
                <img className='object-contain' src={dedo} alt='Deslizar' />
              </div>
            </div>
            <div className='px-2 pb-12'>
              <div>
                <img className='object-contain' src={tituloPreparacion} alt='Titulo Preparacion' />
              </div>
              <div>
                <div className='grid grid-cols-2 gap-4'>
                  { dataDOM.preparacion && dataDOM.preparacion.map((paso, index) => {
                    const { id, imagen, texto, alt, center } = paso;
                    const ultimo = dataDOM.preparacion.length;
                    return (
                      <React.Fragment key={id}>
                        {index + 1 !== ultimo ?
                          (
                            <div className='grid grid-cols-12 gap-2'>
                              <div className='col-span-5 flex justify-center items-start'>
                                <img className='object-contain w-full sm:w-24 max-w-xs' src={imagen} alt={alt} />
                              </div>
                              <div className='col-span-7'>
                                <p className='gotham-m text-gray-700 text-xxs'>{texto}</p>
                              </div>
                            </div>
                          ) : (
                            <div className={`flex justify-center items-center w-full h-full px-6 ${center && 'col-span-2'}`}>
                              <img className='object-contain w-40 sm:w-48 max-w-xs' src={imagen} alt={alt} />
                            </div>
                          )}
                      </React.Fragment>
                    );
                  }) }
                </div>
              </div>
            </div>
            <div className='home-table py-2'>
              <h3 className='text-white text-center text-2xl gotham-m pb-4'>Vida √∫til y conservaci√≥n</h3>
              <div className='flex justify-center'>
                <table className='gotham-l text-left'>
                  <thead className='border-b-2 border-white'>
                    <tr className='text-white'>
                      <th className='p-3' scope='col'>Condici√≥n</th>
                      <th aria-label='image' scope='col' />
                      <th className='p-3' scope='col'>Tiempo</th>
                    </tr>
                  </thead>
                  <tbody>
                    { dataDOM.vidaUtil && dataDOM.vidaUtil.map((objeto, index) => {
                      const { id, descripcion, noBorder, borderFecha, imgClass, img, alt, fecha } = objeto;
                      return (
                        <tr className={noBorder ? null : 'border-b border-white'} key={id}>
                          <>
                            <th className='p-3 text-white'>{descripcion}</th>
                            <td className={`flex justify-center ${imgClass || 'p-3'}`}>
                              <img className='object-contain' src={img} alt={alt} />
                            </td>
                            <td className={`home-table-tiempo p-3 ${borderFecha && 'border-b border-white'}`}>{fecha}</td>
                          </>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          </main>
        </section>
      </div>
    </>
  );
};

export default Home;
