import React from 'react';
import { Link } from 'react-router-dom';
//import { Link } from 'gatsby';
import { setCookie } from '../localFunctions';
import { SEO } from '../components';

const Prueba = () => {
  const go = (donde) => {
    setCookie('mixes', donde, 365);
    setCookie('apertura', 1, 365);
    document.querySelector('.irHome').classList.replace('hidden', 'flex');
  };
  return (
    <>
      <SEO title='Prueba' />
      <div>
        <div className='grid grid-cols-3 gap-4'>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(1)}>
              Bizcochuelo
            </button>
          </div>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(2)}>
              Chantilly
            </button>
          </div>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(3)}>
              Pastelera
            </button>
          </div>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(4)}>
              Merengue
            </button>
          </div>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(5)}>
              Mousse
            </button>
          </div>
          <div>
            <button type='button' className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' onClick={() => go(6)}>
              MixSablee
            </button>
          </div>
        </div>
        <div className='irHome hidden justify-center animated fadeIn mt-6'>
          <Link className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded' to='/'>Ir al Home</Link>
        </div>
      </div>
    </>
  );
};

export default Prueba;
