import React from 'react';
import { useHistory } from 'react-router-dom';
//import { navigate } from 'gatsby';
import mixesKeuken from '../assets/static/btn_mixeskeuken.png';
import moka from '../assets/static/moka.jpg';
import conito from '../assets/static/conito.jpg';
import paleta from '../assets/static/paleta.jpg';
import concorde from '../assets/static/concorde.jpg';
import shot from '../assets/static/shot.jpg';
import masTips from '../assets/static/mastips.png';
import '@fortawesome/fontawesome-free/css/all.css';
import { Header, SelectReceta, SEO } from '../components';
import { go } from '../localFunctions';

const Recetas = () => {
  const history = useHistory();
  const navigate = history.push;
  return (
    <>
      <SEO title='Recetas' />
      <div>
        <Header>
          <div className='mt-3 text-center home_header-titulo'>
            <div className='flex justify-center pb-1'>
              <img className='object-contain w-16 h-12' src={mixesKeuken} alt='Mixes Keuken' />
            </div>
            <p className='pb-1 text-xs font-bold leading-snug text-white gotham-l'>PROB√Å LAS MEJORES RECETAS COMBINANDO NUESTROS PRODUCTOS</p>
          </div>
          <div />
        </Header>
        <section className='wrapper animated fadeIn'>
          <main>
            <div className='grid grid-cols-2 divide-x-2 divide-y-4 divide-white'>
              <SelectReceta src={moka} alt='Torta Moka' num={1} dataSend={2} />
              <SelectReceta src={conito} alt='Conitos de dulce de leche' num={2} dataSend={0} />
              <SelectReceta src={paleta} alt='Paletas de merengue' num={3} dataSend={1} />
              <SelectReceta src={concorde} alt='Torta Concorde' num={4} dataSend={3} />
              <SelectReceta src={shot} alt='Shot Tentaci√≥n' num={5} dataSend={4} />
              <button type='button' onClick={() => go('.wrapper', () => navigate('/MasTips'))}>
                <img className='object-contain rounded' alt='Mas Tips' src={masTips} />
              </button>
            </div>
          </main>
        </section>
      </div>
    </>
  );
};

export default Recetas;
