import React, { useState, useEffect } from 'react';
import { animateCSS, postWebService, swiperResize, getCookie, setCookie } from '../localFunctions';
import '../assets/styles/slide.css';
import packBizcochuelo from '../assets/static/pack_bizcochuelo.png';
import packChantilly from '../assets/static/pack_chantilly.png';
import packMerengue from '../assets/static/pack_merengue.png';
import packMousse from '../assets/static/pack_mousse.png';
import packPastelera from '../assets/static/pack_pastelera.png';
import packSablee from '../assets/static/pack_sablee.png';
import galletasChocolate from '../assets/static/galletasChocolate.png';
import chips from '../assets/static/Chips Alpino Semiamargo.png';
import uno from '../assets/static/1.png';
import dos from '../assets/static/2.png';
import tres from '../assets/static/3.png';
import cuatro from '../assets/static/4.png';
import cinco from '../assets/static/5.png';
import seis from '../assets/static/6.png';
import heart from '../assets/static/heart2.png';
import heartWhite from '../assets/static/heart.png';
import Modal from './Modal';

const Slide = ({ indexParent, img, alt, ingredientes, is }) => {
  const RECETA = is === 'conito' ? '06' : is === 'paleta' ? '07' : is === 'moka' ? '08' : is === 'concorde' ? '09' : is === 'shot' && '10';
  const [ingredienteActual, setIngredienteActual] = useState(0);
  const [windowState, setWindowState] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [isActive, setIsActive] = useState(typeof window !== 'undefined' ? getCookie(`meGusta${indexParent}`) : null);
  const handleChangeIngrediente = (index) => {
    animateCSS(`#paquete${indexParent}${index}`, 'pulse');
    animateCSS(`.cuerpo${indexParent}`, 'fadeOut faster', () => {
      setIngredienteActual(index);
      animateCSS(`.cuerpo${indexParent}`, 'fadeIn faster', () => {
        swiperResize();
      });
    });
  };

  const meGusta = () => {
    animateCSS(`#btnMegusta${indexParent}`, 'pulse');
    if (!document.querySelector(`.botonMeGusta${indexParent}`).classList.contains('btn-active')) {
      setCookie(`meGusta${indexParent}`, true);
      postWebService(`sdesc=0000600107000${RECETA}`);
      setIsActive(true);
      setIsOpen(true);
      //Aca se abriria el modal
    } else {
      setCookie(`meGusta${indexParent}`, '');
      setIsActive(false);
    }
  };

  const onClose = () => {
    animateCSS('.Modal', 'fadeOut faster', () => {
      setIsOpen(false);
    });
    animateCSS('.Modal__container', 'slideOutUp faster');
  };

  const descargarReceta = () => {
    postWebService(`sdesc=0000600108000${RECETA}`);
    switch (indexParent - 1) {
      case 0:
        window.open('http://www.mixeskeuken250gr.com.ar/docs/Receta Torta Moka LODISER.pdf');
        break;
      case 1:
        window.open('http://www.mixeskeuken250gr.com.ar/docs/Receta Conitos Dulce de leche LODISER.pdf');
        break;
      case 2:
        window.open('http://www.mixeskeuken250gr.com.ar/docs/Receta Paletas de Merengue LODISER.pdf');
        break;
      case 3:
        window.open('http://www.mixeskeuken250gr.com.ar/docs/Receta Torta Concorde LODISER.pdf');
        break;
      case 4:
        window.open('http://www.mixeskeuken250gr.com.ar/docs/Receta Shot Tentacion LODISER.pdf');
        break;
    }
  };

  useEffect(() => {
    if (typeof window !== 'undefined') {
      setWindowState(true);
    }
  }, []);

  return (
    <div>
      <Modal isOpen={isOpen} onClose={onClose} />
      <div>
        <img className='object-contain w-full' src={img} alt={alt} />
      </div>
      <div className='grid grid-cols-12 py-2 bg-receta-interactuar'>
        <div className='col-span-7'>
          <button className='ml-2 text-sm font-bold gotham-l' type='button' onClick={descargarReceta}>
            DESCARGAR RECETA
            {' '}
            <span className='icon-download2' />
          </button>
        </div>
        <div id={`btnMegusta${indexParent}`} className='flex justify-end col-span-5 mr-2'>
          <button
            type='button'
            className={`border-2 rounded-full flex items-center border-red-600 font-bold px-3 botonMeGusta${indexParent} ${isActive ? 'btn-active' : 'text-red-700'}`}
            onClick={meGusta}
          >
            <img className='object-contain w-6 mr-2' src={isActive ? heartWhite : heart} alt='heart' />
            Me gusta
          </button>
        </div>
      </div>
      <div>
        <p className='py-2 ml-6 gotham-m'>Hac√© click en cada ingrediente:</p>
        <nav>
          <ul className='flex flex-wrap justify-center'>
            {windowState && ingredientes.map((ingrediente, index) => {
              const { id, src, texto, final, noClick } = ingrediente;
              let img;
              switch (src) {
                case 'packBizcochuelo':
                  img = packBizcochuelo;
                  break;
                case 'packChantilly':
                  img = packChantilly;
                  break;
                case 'packMerengue':
                  img = packMerengue;
                  break;
                case 'packMousse':
                  img = packMousse;
                  break;
                case 'packPastelera':
                  img = packPastelera;
                  break;
                case 'packSablee':
                  img = packSablee;
                  break;
                case 'chips':
                  img = chips;
                  break;
                case 'galletasChocolate':
                  img = galletasChocolate;
                  break;
              }
              return (
                <li onClick={!noClick && (() => handleChangeIngrediente(index))} className={`buttonIngrediente px-2 ${final ? 'w-full' : 'w-4/12'}`} id={`paquete${indexParent}${index}`} key={`ingrediente${indexParent}${id}`}>
                  <div className='animated pulse slow'>
                    { final ? src ? (
                      <div className='flex flex-col items-center justify-center p-1 mt-2 transition duration-500 rounded'>
                        <div className={`rounded p-1 transition duration-500 ${ingredienteActual + 1 === id && 'border-2 border-red-600'}`}>
                          <img className='object-contain w-24' src={img} alt={src} />
                          <div className='text-center'>
                            <p className='font-semibold'>{texto}</p>
                          </div>
                        </div>
                      </div>
                    ) : (
                      <div className='flex justify-center mt-2 text-center transition'>
                        <p className={`p-1 ${ingredienteActual + 1 === id ? 'border-2' : 'border'} border-red-600 rounded font-semibold`}>
                          {texto}
                        </p>
                      </div>
                    ) :
                      (
                        <div className={`p-1 rounded transition duration-500 ${ingredienteActual + 1 === id && 'border-2 border-red-600'}`}>
                          <div>
                            <img className='object-contain' src={img} alt={src} />
                          </div>
                          <div className='text-center'>
                            <p className='font-semibold'>{texto}</p>
                          </div>
                        </div>
                      )}
                  </div>
                </li>
              );
            })}
          </ul>
        </nav>
      </div>
      <div className={`fast mt-4 cuerpo${indexParent}`}>
        {windowState && ingredientes[ingredienteActual].lista && (
          <div>
            <div>
              <h2 className='text-3xl font-medium text-center text-gray-800'>Ingredientes</h2>
            </div>
            <ul className='grid grid-cols-2 row-gap-2 col-gap-10 px-6 mt-1 list-disc'>
              {ingredientes[ingredienteActual].lista.map((element, index) => {
                const { id, name, cant } = element;
                return (
                  <li className='font-bold text-gray-800' key={id}>
                    {name}
                    {cant && (
                      <>
                        <br />
                        <span className='font-normal'>{cant}</span>
                      </>
                    )}
                  </li>
                );
              })}
            </ul>
          </div>
        )}
        <div className='mt-4'>
          <div>
            <h2 className='text-3xl font-medium text-center text-gray-800'>Paso A Paso</h2>
          </div>
          <div>
            { windowState && ingredientes[ingredienteActual].pasos && ingredientes[ingredienteActual].pasos.map((paso, index) => {
              const { id, texto, em } = paso;
              let img;
              switch (index + 1) {
                case 1:
                  img = uno;
                  break;
                case 2:
                  img = dos;
                  break;
                case 3:
                  img = tres;
                  break;
                case 4:
                  img = cuatro;
                  break;
                case 5:
                  img = cinco;
                  break;
                case 6:
                  img = seis;
                  break;
                default:
                  break;
              }
              return (
                <React.Fragment key={id}>
                  <div className='grid grid-cols-12 px-2 py-6'>
                    <div className='flex items-center justify-center col-span-4'>
                      <img className='object-contain w-16' src={img} alt={index} />
                    </div>
                    <div className='col-span-8 leading-snug text-center text-gray-700 gotham-m'>
                      <p>
                        {!em ? texto :
                          (
                            <>
                              {texto}
                              <br />
                              <em>{em}</em>
                            </>
                          ) }
                      </p>
                    </div>
                  </div>
                  { ingredientes[ingredienteActual].pasos.length !== index + 1 && <hr className='slide-hr' /> }
                </React.Fragment>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Slide;
