import React from 'react';
import { useHistory } from 'react-router-dom';
import '../assets/styles/header.css';
//import { navigate } from 'gatsby';
import { go } from '../localFunctions';
import homeNegativa from '../assets/static/home_negativa.png';

const Header = ({ children, noHome }) => {
  const history = useHistory();
  const navigate = history.push;
  return (
    <header className='header pt-2 px-4 sticky top-0 left-0 w-full z-10'>
      {!noHome && (
        <div className='flex justify-end'>
          <button type='button' onClick={() => go('.wrapper', () => navigate('/'))}>
            <img className='w-6 object-contain' src={homeNegativa} alt='Volver a home' />
          </button>
        </div>
      )}
      {children}
    </header>
  );
};

export default Header;
