import React from 'react';
import Slider from 'react-slick';
import { Header, Slide, SEO } from '../components';
import { animateCSS, getCookie } from '../localFunctions';
import json from '../jsons/receta.json';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import '../assets/styles/slickStyle.css';
import '../assets/styles/receta.css';
import moka from '../assets/static/moka.jpg';
import conito from '../assets/static/conito.jpg';
import paleta from '../assets/static/paleta.jpg';
import concorde from '../assets/static/concorde.jpg';
import shot from '../assets/static/shot.jpg';

class Receta extends React.Component {
  constructor(props) {
    super(props);
    if (typeof window !== 'undefined') {
      this.next = this.next.bind(this);
      this.previous = this.previous.bind(this);
      this.position = parseInt(getCookie('recetaSlide'), 10);
      this.state = {
        title: json[this.position].title,
      };
    }
    this.verifyArrow = (next) => {
      if (typeof window !== 'undefined') {
        if (next === 0) {
          if (document.querySelector('.flechaIzquierda').classList.contains('left-active')) {
            document.querySelector('.flechaIzquierda').classList.remove('left-active');
            document.querySelector('.flechaIzquierda').classList.add('left');
          }
        } else if (next + 1 === json.length) {
          if (document.querySelector('.flechaDerecha').classList.contains('right-active')) {
            document.querySelector('.flechaDerecha').classList.remove('right-active');
            document.querySelector('.flechaDerecha').classList.add('right');
          }
          if (document.querySelector('.flechaIzquierda').classList.contains('left')) {
            document.querySelector('.flechaIzquierda').classList.remove('left');
            document.querySelector('.flechaIzquierda').classList.add('left-active');
          }
        } else {
          if (document.querySelector('.flechaDerecha').classList.contains('right')) {
            document.querySelector('.flechaDerecha').classList.remove('right');
            document.querySelector('.flechaDerecha').classList.add('right-active');
          } else if (document.querySelector('.flechaIzquierda').classList.contains('left')) {
            document.querySelector('.flechaIzquierda').classList.remove('left');
            document.querySelector('.flechaIzquierda').classList.add('left-active');
          }
        }
      }
    };
  }

  componentDidMount() {
    if (typeof window !== 'undefined') {
      this.verifyArrow(this.position);
    }
  }

  next() {
    this.slider.slickNext();
  }

  previous() {
    this.slider.slickPrev();
  }

  render() {
    let titleDom = '';
    const settings = {
      infinite: false,
      initialSlide: this.position,
      adaptiveHeight: true,
      arrows: false,
      beforeChange: (current, next) => {
        this.verifyArrow(next);
        this.setState({
          title: json[next].title,
        });
        animateCSS('.header-title', 'slideInDown');
      },
    };
    if (typeof window !== 'undefined') {
      const { title } = this.state;
      titleDom = title;
    }
    return (
      <>
        <SEO title='Receta' />
        <div className='receta'>
          <Header>
            <div>
              <h1 className='text-lg text-center text-white header-title'>{titleDom}</h1>
              <div className='flex justify-between py-2'>
                <div aria-label='Next Slide' role='button' tabIndex={0} className='w-5 h-10 flechaIzquierda left' onClick={this.previous} />
                <div aria-label='Prev Slide' role='button' tabIndex={0} className='w-5 h-10 flechaDerecha right-active' onClick={this.next} />
              </div>
            </div>
          </Header>
          <div className='overflow-hidden wrapper'>
            <Slider ref={(c) => (this.slider = c)} {...settings}>
              {json.map((receta) => {
                const { id, src, alt, ingredientes } = receta;
                let img;
                switch (src) {
                  case 'moka':
                    img = moka;
                    break;
                  case 'conito':
                    img = conito;
                    break;
                  case 'paleta':
                    img = paleta;
                    break;
                  case 'concorde':
                    img = concorde;
                    break;
                  case 'shot':
                    img = shot;
                    break;
                }
                return (
                  <Slide key={id} indexParent={id} is={src} img={img} alt={alt} ingredientes={ingredientes} />
                );
              })}
            </Slider>
          </div>
        </div>
      </>
    );
  }
};

export default Receta;
