import React, { useEffect, useState } from 'react';
import json from '../jsons/masTips.json';
import tips from '../assets/static/tips.png';
import { getCookie } from '../localFunctions';
import { Header, SEO } from '../components';

const MasTips = () => {
  const [dataDom, setDataDom] = useState({
    img: '',
    alt: '',
  });

  useEffect(() => {
    let mixes = parseInt(getCookie('mixes'), 10);
    const subMixes = parseInt(getCookie('subMixes'), 10);
    if (subMixes !== 0 && subMixes !== undefined) {
      mixes = subMixes;
    }
    mixes = mixes - 1;
    setDataDom({ ...dataDom,
      img: json[mixes].img,
      alt: json[mixes].alt,
    });
  }, []);

  return (
    <>
      <SEO title='M√°s Tips' />
      <div>
        <Header>
          <div className='py-3'>
            <div className='flex justify-center pb-1'>
              <img className='object-contain h-16' src={tips} alt='Mixes Keuken' />
            </div>
          </div>
          <div />
        </Header>
        <section className='wrapper animated fadeIn'>
          <main>
            <div className='w-full px-4 pt-12 pb-4 flex justify-center'>
              <img className='animated tada object-contain' src={dataDom.img} alt={dataDom.alt} />
            </div>
          </main>
        </section>
      </div>
    </>
  );
};

export default MasTips;
