import React from 'react';
import { useHistory } from 'react-router-dom';
//import { navigate } from 'gatsby';
import { animateCSS, postWebService, setCookie } from '../localFunctions';

const SelectReceta = ({ src, alt, num, dataSend }) => {
  const history = useHistory();
  const navigate = history.push;

  const goTo = () => {
    const PRIMERA_NEW_RECETAS = 6;
    const sum = dataSend + PRIMERA_NEW_RECETAS;
    const result = sum < 10 ? `0${sum}` : sum;
    setCookie('recetaSlide', num - 1);
    postWebService(`sdesc=0000600105000${result}`);
    animateCSS('.wrapper', 'fadeOut faster', () => {
      navigate('/Receta');
    });
  };

  return (
    <button className='relative flex items-start justify-start w-full h-full rounded' type='button' onClick={() => goTo()}>
      <img className='object-contain w-full h-full rounded' alt={alt} src={src} />
      <div className='absolute bottom-0 w-full px-4 pb-2'>
        <h4 className='text-2xl text-left text-white gotham-l'>{alt}</h4>
      </div>
    </button>
  );
};

export default SelectReceta;
