import React from 'react';
import '../assets/styles/radioButton.css';

const RadioButton = ({ id, texto, className }) => {

  const radioButton = () => {
    const botonesRadio = document.querySelectorAll(`.${className}`);
    for (let i = 0; i < botonesRadio.length; i++) {
      const botonRadio = botonesRadio[i];
      if (!botonRadio.classList.contains('hidden')) {
        botonRadio.classList.add('hidden');
        break;
      }
    }
    botonesRadio[id].classList.remove('hidden');
  };

  return (
    <div className='flex flex-col items-center' role='button' tabIndex={0} onClick={radioButton}>
      <span className='w-5 h-5 border-3 radio-border bg-white rounded-full flex justify-center items-center'>
        <span className={`w-2 h-2 bg-radio rounded-full hidden ${className}`} />
      </span>
      <p className='radio-text text-center text-xs pt-1'>{texto}</p>
    </div>
  );
};

export default RadioButton;
